import API from "./axios";

export const PostRequest = (url: string, data?: object) => {
  return new Promise<object>(async (resolve, reject) => {
    await API.post(url, data)
      .then((response: any) => {
        if (response) resolve(response);
        else reject("Не удалось получить данные с сервера");
      })
      .catch(async (err:any) => {
        reject(err);
      });
  });
};

export const GetRequset = (
  url: string,
  params?: object,
  _arguments?: object
) => {
  return new Promise(async (resolve, reject) => {
    await API.get(url, { params: params, ..._arguments })
      .then((response: any) => {
        if (response) resolve(response);
        else reject("Не удалось получить данные с сервера");
      })
      .catch((err:any) => {
        reject(err);
      });
  });
};
