import { useEffect, useState } from "react";
import style from "./index.module.css";
import { GetRequset } from "../shared/API/apiAxios.tsx";
import {
  expandViewport,
  initData,
  qrScanner,
  retrieveLaunchParams,
} from "@telegram-apps/sdk";

export const StartPage = () => {
  const [data, SetData] = useState();
  const { initDataRaw } = retrieveLaunchParams();

  useEffect(() => {
    expandViewport();

  }, []);

  
  setInterval(() => () => console.log(initDataRaw), 2000);

  const callbackQrCode = (res) => {
    SetData(JSON.stringify(res));
    qrScanner.close();
  };

  const testRequest = () => {
    //нужен https
    GetRequset("/ping", { test: "123" })
      .then((res) => SetData(res.data))
      .catch((err) => SetData(err));
  };
  //tg://resolve?domain=test_m_ksu_bot&start=123123

  return (
    <div className={style.main}>
      <div className={style.head}></div>

      <button onClick={() => testRequest()}>Test request</button>
      <span style={{ color: "white" }}>{data}</span>

      <button
        onClick={() =>
          qrScanner.open({
            text: "Наведите камеру на QR код",
            onCaptured(res) {
              callbackQrCode(res);
            },
          })
        }
      >
        QR CODE SCANNER
      </button>
      {/* <span>{telegram?.initData}</span> */}

      {/* <button onClick={() => sendDataToServer()}>TEST BUTTON</button> */}
    </div>
  );
};
